import { Suspense, lazy } from 'react';
import { Navigate, useRoutes, useLocation } from 'react-router-dom';
// layouts
import DashboardLayout from '../layouts/dashboard';
import LogoOnlyLayout from '../layouts/LogoOnlyLayout';

// guards
import GuestGuard from '../guards/GuestGuard';
import AuthGuard from '../guards/AuthGuard';

// components
import LoadingScreen from '../components/LoadingScreen';

// ----------------------------------------------------------------------

const Loadable = (Component) => (props) => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const { pathname } = useLocation();
  const isDashboard = pathname.includes('/dashboard');

  return (
    <Suspense
      fallback={
        <LoadingScreen
          sx={{
            ...(!isDashboard && {
              top: 0,
              left: 0,
              width: 1,
              zIndex: 9999,
              position: 'fixed',
            }),
          }}
        />
      }
    >
      <Component {...props} />
    </Suspense>
  );
};

export default function Router() {
  return useRoutes([
    {
      path: 'auth',
      children: [
        {
          path: 'login',
          element: (
            <GuestGuard>
              <Login />
            </GuestGuard>
          ),
        },
      ],
    },

    {
      path: 'dashboard',
      element: (
        <AuthGuard>
          <DashboardLayout />
        </AuthGuard>
      ),
      children: [
        { path: '/', element: <Navigate to="/dashboard/app" replace /> },
        { path: 'app', element: <div>app</div> },
        {
          path: 'brand',
          children: [
            { path: '/', element: <Navigate to="/dashboard/brand/list" replace /> },
            { path: 'list', element: <div>brand list</div> },
            { path: 'new', element: <BrandCreate /> },
            { path: '/:name/edit', element: <BrandCreate /> },
            { path: '/:name/view', element: <BrandView /> },
          ],
        },
        {
          path: 'campaign',
          children: [
            { path: '/', element: <Navigate to="/dashboard/survey/list" replace /> },
            { path: 'list', element: <CampaignList /> },
            { path: '/:name/detail', element: <CampaignDetail /> },
            { path: 'new', element: <CampaignCreate /> },
          ],
        },
        {
          path: 'survey',
          children: [
            { path: '/', element: <Navigate to="/dashboard/survey/list" replace /> },
            { path: 'list', element: <div>survey list</div> },
          ],
        },
        {
          path: 'task',
          children: [
            { path: '/', element: <Navigate to="/dashboard/task/list" replace /> },
            { path: 'list', element: <div>task list</div> },
          ],
        },
        {
          path: 'contest',
          children: [
            { path: '/', element: <Navigate to="/dashboard/contest/list" replace /> },
            { path: 'list', element: <div>contest list</div> },
          ],
        },
      ],
    },

    {
      path: '*',
      element: <LogoOnlyLayout />,
      children: [
        { path: '404', element: <div>404 not found</div> },
        { path: '*', element: <Navigate to="/404" replace /> },
      ],
    },

    {
      path: '/',
      element: <Navigate to="/dashboard/app" replace />,
    },

    // 404
    { path: '*', element: <Navigate to="/404" replace /> },
  ]);
}

// IMPORT COMPONENTS
const Login = Loadable(lazy(() => import('../pages/Login')));
const BrandCreate = Loadable(lazy(() => import('../pages/BrandCreate')));
const BrandView = Loadable(lazy(() => import('../pages/BrandView')));
const CampaignList = Loadable(lazy(() => import('../pages/CampaignList')));
const CampaignCreate = Loadable(lazy(() => import('../pages/CampaignCreate')));
const CampaignDetail = Loadable(lazy(() => import('../pages/CampaignDetail')));
